import React from 'react'

// Import typefaces
import 'typeface-montserrat'
import 'typeface-merriweather'

import {rhythm} from '../utils/typography'
const pic = '/Rose_Cross_Lamen.jpg'

class Bio extends React.Component {
  render() {
    return (
      <div style={{
        marginBottom: rhythm(2.5)
      }}>

        <h3>Сайт "Саентология Тома Круза"</h3>
        <img src={pic} alt={`Саентология`} style={{
          float: 'right',
          marginRight: rhythm(1 / 4),
          marginBottom: 0,
          width: rhythm(6),
          height: rhythm(7.83)
        }}/>
        <i>- Я вам деньги платил?<br/>
          - Платил.<br/>
          - Вы меня одитировали?<br/>
          - Одитировали.<br/>
          - Где мои сверхспособности?<br/>
          - Какие сверхспособности?</i><br/><br/>
        На этом сайте мы публикуем объективную информацию о культе саентологии - организованной псевдорелигиозной коммерческой структуре.<br/>
        Сайт создаётся совместно с его читателями и будет собирать объективную информацию о культе саентологии и обо всём, что с ним связано.<br/>Пишите в комментах Ваши предложения о том, что разместить в новых статьях!
      </div>
    )
  }
}

export default Bio



